  transition: all 1s ease-in-out;
  transform-style: preserve-3d;
}
.dream .flip-item-text {
  position: absolute;
  bottom: 8px;
  left: 0;
  width: 100%;
  text-align: center;
  color: #fff;
}
.dream .flip-item-front {
  transform: rotateY(0deg);
  z-index: 2;
  background: url(/static/img/postcard-back.570b73a.png) no-repeat;
  background-size: cover;
}
.dream .flip-item-back {
  transform: rotateY(180deg);
  z-index: 1;
  background: url(/static/img/postcard-write.95bed31.png) no-repeat;
  background-size: cover;
}
.dream .is-click {
  top: 1.2rem;
  left: 0.29rem;
  height: 3.87rem;
  width: 6.74rem;
}
.dream .is-click-not {
  opacity: 0;
}
.dream .is-click-two {
  transition: height 1s ease-in-out 1s;
  top: 1.2rem;
  left: 0.29rem;
  height: 3.87rem;
  width: 6.74rem;
  animation: showX ;
}
.dream .is-click-two .flip-item-front {
  z-index: 1;
  transform: rotateY(180deg);
}
.dream .is-click-two .flip-item-back {
  z-index: 2;
  transform: rotateY(0deg);
}
.dream .is-click-to-right {
  transition: all 1.5s ease-in-out;
  right: 0.3rem;
  left: -3.24rem;
}
.dream .is-click .flip-item-front {
  z-index: 1;
  transform: rotateY(180deg);
}
.dream .is-click .flip-item-back {
  z-index: 2;
  transform: rotateY(0deg);
}
.dream .flip-title-select {
  position: absolute;
  background: url(/static/img/select-text.c116518.png) no-repeat;
  background-size: cover;
  width: 2.6rem;
  height: 0.3rem;
  top: 0.38rem;
  left: 0.38rem;
  opacity: 1;
  transition: opacity 1s ease-in-out;
}
.dream .flip-title-select-after {
  opacity: 0;
}
.dream .flip-title-write {
  position: absolute;
  background: url(/static/img/write-dream-text.e01ae5c.png) no-repeat;
  background-size: cover;
  width: 2.69rem;
  height: 0.58rem;
  top: 0.38rem;
  left: 0.38rem;
  opacity: 0;
  transition: opacity 1s ease-in-out 1s;
}
.dream .flip-title-write-after {
  opacity: 1;
}
@keyframes showX {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
